import React, { Component } from 'react';
import { connect } from "react-redux";
import { baseUrl } from '../../../../../../Assets/AssetsHelper';
import { GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../../../Services/Utils/utils';
import { Button } from 'react-bootstrap';
class NetworkList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            networkList: props.networkList,
            networkListLoading:false
        }
    }
    componentDidMount(){
        const { networkList} = this.state;
        if(networkList && networkList.length > 0 ){
            this.setState({ networkListLoading: false })
        }else{
            this.setState({ networkListLoading: true })
        }
    }

    filterNetworkList = (e) => {
        let filteredList = this.filterList(e.target.value);
        this.setState({ textValue: e.target.value, networkList: filteredList });
    }

    filterList = (value) => {
        const { networkList } = this.props;
        if (networkList.length > 0) {
            return networkList.filter(network => network.name.toLowerCase().match(value.toLowerCase()));
        }
        return [];
    }

    uploadType = (uploadType, network) => {
        this.props.uploadTypeCallback(uploadType, network)

        this.setState({
            manualUpload:uploadType == 'uploadAssets' ? true : false
        })
    };

    componentWillReceiveProps(nextProps) {
        if (this.state.networkList != nextProps.networkList) {
            this.setState({ networkList: nextProps.networkList,networkListLoading: false  })
        }else{
            this.setState({ networkListLoading: this.props.networkList > 0 ? true : false})
        }
    }
    render() {
        const { networkList, networkListLoading } = this.state;
        const numbers = Array.from({length: 18}, (_, i) => i + 1);
        return   <div className="w-100 my-5 block-center" style={{minHeight:'calc(100vh - 150px)'}}>
                        <div className="d-flex flex-column">
                            <h2 className="fs-6 mb-lg-10 mb-5 text-center">First, select where you want the assets to come from.</h2>
                            <ul className='network_list list-inline block-center flex-wrap w-100'>
                                {!networkListLoading ? 
                                    networkList && networkList.length > 0 ? 
                                    <>
                                   {networkList.map((network, index) => {
                                        return <li key={index}><Button variant='secondary' data-id={network.id} className={`fs-7 fw-medium block-center d-flex w-100`} 
                                        onClick={() => this.uploadType(network.id === 29 ? 'onsite-upload' : 'byNetwork', network)}>
                                            <span className="symbol symbol-24 me-2 bg-light">
                                                <img className="object-center-contain" src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(network.id)}.svg?3432`)} alt={GET_NETWORK_NAME(network.id)} height={30} width={30} />
                                            </span>
                                            {GET_NETWORK_NAME(network.id)}
                                        </Button></li>
                                    })}
                                    <li><Button variant='outline-secondary' data-id={0} className={`fs-7 fw-medium block-center d-flex w-100 border-dashed border-dark btn-active-light`} 
                                        onClick={() => this.uploadType('uploadAssets', null)}>
                                        <span className="symbol symbol-22 me-2 bg-light">
                                            <img className="object-center-contain" src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(0)}.svg`)} alt={GET_NETWORK_NAME(0)} height={30} width={30} />
                                        </span>
                                        {GET_NETWORK_NAME(0)}
                                    </Button></li>
                                    </>
                                    : <span className="py-5 text-center d-block fs-8 fw-medium text-gray-500">No Network Found</span>: 
                                    numbers.map((number, index) => (
                                        <li key={index}><span className="sk_rect h-40px me-2 w-100"></span></li>
                                    ))
                                }
                            </ul>
                        </div>
                    </div>
    }
}


const mapStateToProps = (state) => {
    return {
        networkData: state.networks.networkData
    }
}
export default connect(mapStateToProps)(NetworkList);